body {
  margin: 0px;
  overflow: hidden;
}
canvas {
  height: 100%;
  width: 100%;
}
.depthBox {
  border-top: solid 10px #138f1b;
  border-bottom: solid 10px #138f1b;
  border-left: solid 10px #0f5713;
  border-right: solid 10px #0f5713;
}
.depthBox.error {
  border-color: #ff0000;
}
#flashMessage {
  z-index: 100;
  opacity: 0.8;
  display: none;
  background: #eeeeee;
  color: #000000;
  width: 300px;
  padding: 10px;
  position: absolute;
  left: 50%;
  margin-left: -150px;
  bottom: 20px;
  text-align: center;
}
.screen {
  z-index: 200;
  display: none;
  background-color: #dddddd;
  opacity: 0.8;
  width: 500px;
  margin: auto;
  padding: 10px;
  position: absolute;
  left: 50%;
  margin-left: -250px;
}
.screen h2 {
  text-align: center;
}
.screen p {
  text-align: center;
}
.screen ul {
  padding: 0;
}
.screen ul li {
  list-style: none;
  text-align: center;
  font-size: 20px;
  margin: 8px 0;
  /* Menu links */
}
.screen ul li a {
  text-decoration: none;
  background: rgba(255, 255, 255, 0.1);
  -webkit-transition: background 0.2s, color 0.2s;
  -moz-transition: background 0.2s, color 0.2s;
  transition: background 0.2s, color 0.2s;
  position: relative;
  display: inline-block;
  padding: 5px;
  width: 90%;
}
.screen ul li a:after {
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 100%;
  content: '';
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}
.screen ul li a:before {
  speak: none;
  font-size: 48px;
  line-height: 90px;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  display: block;
  -webkit-font-smoothing: antialiased;
}
.screen ul li a:after {
  top: -7px;
  left: -7px;
  padding: 7px;
  box-shadow: 0 0 0 4px #ffffff;
  -webkit-transition: -webkit-transform 0.2s, opacity 0.2s;
  -webkit-transform: scale(0.8);
  -moz-transition: -moz-transform 0.2s, opacity 0.2s;
  -moz-transform: scale(0.8);
  -ms-transform: scale(0.8);
  transition: transform 0.2s, opacity 0.2s;
  transform: scale(0.8);
  opacity: 0;
}
.screen ul li a:hover,
.screen ul li a:focus {
  background: #ffffff;
  color: #41ab6b;
}
.screen ul li a:after {
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
}
.screen ul li a:hover:after,
.screen ul li a:focus:after {
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  opacity: 1;
}
#score {
  position: absolute;
  right: 0;
  top: 0;
  border-bottom: solid 10px #138f1b;
  border-left: solid 10px #0f5713;
  padding: 0 10px;
  background: #eeeeee;
}
.left {
  color: #ff0000;
}
.right {
  color: #0000ff;
}
#scoreLeftDisplay,
#scoreRightDisplay,
#serviceDisplay,
#scoreNeededToWinDisplay,
#maximumContactsAllowedDisplay {
  font-weight: bold;
  font-size: 18px;
}
/* Controls screen */
.playerControl {
  float: left;
  width: 50%;
}
.playerControl p {
  text-align: left;
  padding: 0 0 0 5px;
}
#leftControls {
  border-top: 5px solid #ff0000;
}
#rightControls {
  border-top: 5px solid #0000ff;
}
input.controlKey {
  width: 70px;
  float: right;
  margin-right: 50px;
}
input.controlKey:focus {
  border: 2px solid #41ab6b;
}
/* Rules screen */
#rulesMenu p {
  text-align: left;
}
#rulesMenu input {
  width: 50px;
  float: right;
}
